import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import logo from './logo.svg';
import './App.css';
import 'filepond/dist/filepond.min.css';
import axios from 'axios';
import download from 'downloadjs'
import http from 'http'

const URL = "https://greetez.com:4242/pdf";
const httpAgent = new http.Agent({ keepAlive: true });
const instance = axios.create({
  httpAgent,  // httpAgent: httpAgent -> for non es6 syntax
});



class App extends Component {
  constructor(props) {
    super(props);
    this.state = {email: '', complete: false};
  }


  onChangeHandler=event=>{
    this.setState({
      selectedFile: event.target.files[0],
      loaded: 0,
    })
  }

  onFillHandler=event=>{
    this.setState({
      email: event.target.value,
    });
  }

  onClickHandler = () => {
    if (this.state.selectedFile == null){ 
      alert("Please add a file!");
      return;
    }
    document.getElementById("submitbutton").disabled = true;
    const data = new FormData() 
    data.append('file', this.state.selectedFile)
    data.append('email', this.state.email)
    instance.post(URL, data, { // receive two parameter endpoint url ,form data http://ec2-52-201-42-136.compute-1.amazonaws.com
    responseType: 'text'
      })
      .then(response => {
        const content = response.headers['content-type'];
        document.getElementById('response').innerHTML = response.data;
        // alert(response.data, content)
     })
     .catch(error => console.log(error));
  }
  render() {
    return (
      <div className="App">
        <div className="App-header">
          {/* <img src={logo} className="App-logo" alt="logo" /> */}
          <h2>AILight:</h2>
          <h2>Auto-Highlight Your Document!</h2>
        </div>
        {/* <h1 className="App-intro">
          Upload below...
        </h1> */}
        <br></br>
        <div className="row">
          <div className="col-md-6" style={{float:'none', margin:'auto'}}>
          <TextField id="emailentry" ref='emailentry' label="Email To Send PDF To:" variant="outlined" onChange={this.onFillHandler}/>
              <br></br><br></br>
              <form method="post" action="#" id="#">
                    <div className="form-group files">
                      <input type="file" className="form-control" multiple onChange={this.onChangeHandler}></input>
                      <br></br>
                      <button type="button" id="submitbutton" className="btn btn-success btn-block" onClick={this.onClickHandler}>Highlight</button> 
                    </div>
                </form>
              <h4 id="response"></h4>
              <h4 >What is this?</h4>
              <p>AIlight automatically highlight the important parts in a PDF, making it easier to skim and digest documents. These highlights are attatched to the PDF and will show up on most PDF readers.</p>
              <p>If you don't receive an email in less than 5 minutes please send me an email: anishthite@gmail.com. I would also love any feedback!</p>
          </div>
      </div>
    </div>
    );
  }
}

export default App;



